@import url(https://fonts.googleapis.com/css?family=Poppins:300,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --main-color: #00ccff;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: 'Poppins', sans-serif;
  font-weight: 300;
}

strong { 
  color: #00ccff; 
  color: var(--main-color);
  font-weight: 400;
}

header {
  font-size: 4em;
  margin: 1em;
}

form {
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
  width: 100%;
}

input {
  width: 300px;
  margin: 0 1em;
  padding: 1em;
  font-family: inherit;
  border: 1px solid #555;
  border-radius: 0;
}

button {
  color: white;
  background: #333;
  padding: 0 2em;
  font-family: inherit;
  font-weight: 400;
  font-size: 1em;
  border: none;
  -webkit-transition: .3s;
  transition: .3s;
}

button:hover {
  background: var(--main-color);
  cursor: pointer;
}

.item-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 437px;
  margin-left: -27px;
}

.wrapper {
  flex-wrap: wrap;
  display: flex;
  align-items: center;
  width: 82%;
}

.checkbox {
  margin: 0;
  width: 30px;
  height: 30px;
}

li { -webkit-transition: .5s; transition: .5s }

.li-notdone {
  margin: .7em;
  list-style: none;
  font-size: 1.2em;
}

.li-done {
  color: #BBB;
  margin: .7em;
  list-style: none;
  font-size: 1.2em;
}

.line {
  position: absolute;
  margin: 0 40px;
  width: 280px;
  border-bottom: 1px solid #BBB;
}

.del {
  flex-wrap: wrap;
  background: crimson;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 95px;
  height: 40px;
}

.del:hover {
  background: crimson;
  -webkit-transform: rotate(5deg) scale(1.1);
          transform: rotate(5deg) scale(1.1);
}
